.contacts {
    flex: 1; /* Takes up 20% of the container's height */
    background-color: aquamarine;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 24px;
  }  
  .contact-info {
    display: flex;
    align-items: center;
    margin: 10px;
  }
  
  .contact-info i {
    font-size: 24px;
    margin-right: 10px;
  }