/* Keyframes for the gradient animation */
@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Keyframes for the neuro line animation */
@keyframes neuroLineAnimation {
  0% {
    height: 1px;
  }
  100% {
    height: 200px;
  }
}

